import AdminPagination from 'vue-pagination-2';
export default {
  props: ['records'],
  data() {
    return {
      page: 1,
    };
  },
  components: {
    AdminPagination,
  },
  methods: {
    changePage(page) {
      this.$emit('changePage', page);
    },
  },
};
